.list-pal {
	text-align: center;
	padding: 2rem;
	height: 90vh;
	transform: translateX(-100%);
	animation: animate 0.5s ease-in-out forwards;
	background-color: rgb(13, 14, 25);
}

@keyframes animate {
	to {
		transform: translateX(0);
	}
}

.list-pal h3 {
	padding: 0.8rem;
	color: #e2ecf8;
}

.add-list-contaner {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.add-list-container input {
	padding: 10px;
	width: 8rem;
	margin-left: 30px;
	background-color: #e2ecf8;
}

.add-list-container input::placeholder {
	color: black;
	font-weight: bold;
}

.button {
	color: #e2ecf8;
	padding: 0.65rem;
	border: 2.5px solid #e2ecf8;
	font-size: 1.25rem;
	border-radius: 5px;
	box-shadow: 2px 2px 20px -5px black;
	background: linear-gradient( 135deg, rgb(0, 0, 0) 74%, rgba(255, 255, 255, 0.3) 80%, white 82%, rgba(255, 255, 255, 0.3) 84%, rgb(0, 0, 0) 90%);
    background-size: 1300%;
	background-position:top left;
	animation: bg-flash 4s linear alternate infinite;
    text-shadow: 1px 1px 1px black;
}

@keyframes bg-flash{
	to{
		background-position: bottom right;
	}
}

